alert(Math.PI);
Bmob.initialize("fc8725065ae9442ecf60f048b2b57058","a7c75bd62a6e7e1ec4b73d7f580aafb0");
BmobSocketIo.initialize("fc8725065ae9442ecf60f048b2b57058");
BmobSocketIo.updateTable("chatv2018");
BmobSocketIo.onUpdateTable=function(tablename,data){
  console.log("updata");
  if(tablename=="chatv2018"){
    var line=(data.usr?data.usr:"无名毛玉")+"=&gt;"+data.msg+"<br>";
    console.log(data.msg);
    document.querySelector("#lines").innerHTML+=line;
  }
}
function sendMsg(){
  var Chatv=Bmob.Object.extend("chatv2018");
  var line=new Chatv();
  line.set("msg",prompt("msg"));
  line.set("usr",prompt("usr"));
  line.save(null,{
    success:()=>console.log("ok")
    ,error:()=>console.log("failed")
  });
}
function getAll(){
  var Chatv=Bmob.Object.extend("chatv2018");
  var lines=new Bmob.Query(Chatv);
  lines.find({
    success:(re)=>{
      for(let i=0;i<re.length;i++){
        var line=(re[i].get("usr")?re[i].get("usr"):"无名毛玉")+"=&gt;"+re[i].get("msg")+"<br>";
        console.log(line);
        document.querySelector("#lines").innerHTML+=line;
    }},
    error:()=>console.log("error")
    })
}
window.sendMsg=()=>sendMsg();
window.getAll=()=>getAll();
window.onload=()=>getAll();
document.querySelector("html").onclick=()=>sendMsg();



